.uncheckedBox{
	position: relative; flex-shrink: 0; flex-grow: 0; width: 16px; height: 16px; display: flex; align-items: center; justify-content: center; transition: background 200ms ease-out 0s;
}
.checkboxSquare{
	width: 12px;
	height: 12px;
	display: block;
	fill: white;
	flex-shrink: 0;
	backface-visibility: hidden
}
.checkedBox{
	position: relative;
	flex-shrink: 0;
	flex-grow: 0;
	width: 16px;
	height: 16px;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: background 200ms ease-out 0s;
	background: rgb(35, 131, 226);
}
.check{
	width: 100%;
	height: 100%;
	display: block;
	fill:white;
	flex-shrink: 0;
	backface-visibility: hidden;
}